import React from "react";
import { createMuiTheme } from "@material-ui/core/styles";
import Timeline from "./Timeline";
import Info from "./info";
import Herotext from "./Herotext";
import { ThemeProvider } from "@material-ui/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { fadeIn, fadeInLeft, fadeInRight, fadeInUp } from "react-animations";
import Radium, { StyleRoot } from "radium";
import logo from "./wplogo.png";
import k8s from "./k8s.png";
import heart from "./heart.png";
import "./App.css";

const th = createMuiTheme({
  palette: {
    type: "dark",
  },
});
const useStyles = makeStyles((theme) => ({
  heroButtons: {
    marginTop: th.spacing(4),
  },
  typeography: {
    align: "center",
  },
  heroContent: {
    backgroundColor: th.palette.background.paper,
    padding: th.spacing(3, 0, 1),
  },
}));
const styles = {
  fade: {
    animation: "x 1s",
    animationName: Radium.keyframes(fadeIn, "fade"),
  },
  slideLeft: {
    animation: "x 2s",
    animationName: Radium.keyframes(fadeInLeft, "fadeLeft"),
  },
  slideRight: {
    animation: "x 2s",
    animationName: Radium.keyframes(fadeInRight, "fadeRight"),
  },
  fadeUp: {
    animation: "x 2s",
    animationName: Radium.keyframes(fadeInUp, "fadeUp"),
  },
};

export default function Web() {
  const classes = useStyles();

  return (
    <ThemeProvider theme={th}>
      <React.Fragment>
        <CssBaseline />
        <main>
          <div className={classes.heroContent}>
            <Container>
              <StyleRoot>
                <div>
                  <Typography
                    component="h1"
                    variant="h1"
                    align="center"
                    color="textPrimary"
                  >
                    Welcome to HashiCorp Waypoint
                  </Typography>
                </div>
              </StyleRoot>
            </Container>
          </div>
          <div>
            <Container maxWidth="md">
              <StyleRoot>
                <Grid
                  container
                  // spacing={1}
                  // justify="center"
                  alignItems="center"
                >
                  <Grid item xs>
                    <img
                      class="k8s"
                      style={styles.slideLeft}
                      src={k8s}
                      alt="waypoint"
                    />
                  </Grid>
                  <Grid item xs>
                    <img
                      class="k8s"
                      style={styles.fade}
                      src={heart}
                      alt="waypoint"
                    />
                  </Grid>
                  <Grid item xs>
                    <img
                      class="wpimage"
                      style={styles.slideRight}
                      src={logo}
                      alt="waypoint"
                    />
                  </Grid>
                </Grid>
              </StyleRoot>
              <Herotext />
              <Info />
            </Container>
          </div>
          <Container maxWidth="md">
            <StyleRoot>
              <div style={styles.fadeUp}>
                <Timeline />
              </div>
            </StyleRoot>
          </Container>
        </main>
      </React.Fragment>
    </ThemeProvider>
  );
}
